.info {
    width: 63rem;
    margin: 7.5vh auto 0;
    margin-top: 3vh;
    padding-top: 0.5rem;
    background-color: white;
    border-style: solid;
    font-family: 'VT323', monospace;
    text-align: left;
    -webkit-box-shadow: 4px 4px 4px 0 rgba(0, 0, 0, .2);
    box-shadow: 4px 4px 4px 0 rgba(0, 0, 0, .2);
}

.info-body {
    background-color: #000;
    color: #fff;
    padding: 0.5rem;
    margin: 0 auto;
    height: 100%;
    overflow: hidden;
}

.info-text {
    height: 22.5rem;
    width: 100%;
    overflow-y: scroll;
    scroll-behavior: smooth;
    padding-right: 3rem; /* Increase/decrease this value for cross-browser compatibility */
    box-sizing: initial; /* So the width will be 100% + 17px */
    color: #fff;
}

.info-text p {
    font-size: 1rem !important;
    margin: 3px auto;
    color: #fff;
}

.info-carret {
    font-size: 1rem;
    
}

@media only screen and (max-width:800px) {
    .info {
        width: 100%;
    }
}