/* Fonts */
h1,h2,h3 {
    margin-bottom: 1rem;
}

/* Section Body changes */
.home-section .section-body {
    padding: 2rem;
    padding-top: 0.1rem;
    margin: 0 auto;
    max-width: 62.5rem;
}

/* Intro Sesction */
#intro {
    padding-bottom: 10rem;
    color: #fff;
    min-height: 150vh;
    text-align: left;
    background-color: #000;
}

#intro p {
    font-size: 1.1rem;
    font-family: 'VT323', monospace;

}

#intro h1{
    overflow: hidden;
    white-space: nowrap;
    font-size: 4.5vw;
    font-family: 'VT323', monospace;
    /*border-right: .15em solid white;*/

    animation: typing 5s steps(75),
    blink-caret .75s step-end infinite;

}

/* The typing effect */
@keyframes typing {
  from { width: 0}
  to { width: 100%;}
}


/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: white; }
}

#intro a {
    width: 50%;
    margin: 0 auto;
}

.down-arrow {
    margin: 5rem 0;
    margin:0 auto;
    text-align: center;
    position: static;
}

.down-arrow:hover {
    cursor: pointer;
}

.bounce {
    -moz-animation: bounce 3s infinite;
    -webkit-animation: bounce 3s infinite;
    animation: bounce 3s infinite;
}

.no-display {
    display:none;
}
  
@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-30px);
    }
    60% {
      transform: translateY(-15px);
    }
}

@-webkit-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-30px);
    }
    60% {
      transform: translateY(-15px);
    }
}

@media only screen and (max-width:750px) {
    .down-arrow {
        display: none;
    }
}

 /*Skills Section*/

#skills {
    color: #fff;
    min-height: 175vh;
    background-color: #000;
    margin-top: -10rem;
}

#skills h1{
    font-size: 3rem!important;
    font-family: 'VT323', monospace;
}

#skills h3{
    font-size: 1.5rem!important;
    font-family: 'VT323', monospace;
}

#skills .box section {
    margin: 2rem 0
}

.skills a {
    margin: .5rem!important;
    color: #000;
}

#skills p {
    font-size: 1.2rem;
    font-family: 'VT323', monospace;
}

#skills ul {
    margin: 0;
    padding: 0
}

#skills li{
    font-size: 1.07rem;
}

/* Footer Section */
#footer {
    background-color: #000;
    padding-top: 7.5rem
}

#footer,
#footer a svg {
    color: #fff;
    text-align: center
}

#footer a svg {
    height: 1.5rem;
    width: 2.25rem;
    padding: .25rem 0;
    margin: 5px;
    border: 2px #000; /* TODO */
    border-radius: .25rem;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

#footer a svg:hover {
    color: #000;
    background-color: #fff; /* TODO */
    border-color: #fff
}

#footer .home-section{
    background: #fff;
}

#footer_name{
    font-size: 1.4rem;
    font-family: 'VT323', monospace;
}