.box article {
    flex-grow: 1;
    flex-basis: 0;
    -ms-flex-positive: 1;
    -ms-flex-preferred-size: 0;
    display: block;
    padding: 2rem;
    border: solid 1px;
    background-color: #000;
    color: #fff;
    font-family: 'VT323', monospace;
}
.box article a{
    
    color: white;
}

@media only screen and (max-width:750px) {
    .box article {
        border-right: 0 solid var(--border-color);
        border-bottom: 1px solid var(--border-color);
    }
}
