/* Fonts */
h1,h2,h3 {
    margin-bottom: 1rem;
}

/* Section Body changes */
.home-section .section-body {
    padding: 2rem;
    padding-top: 0.1rem;
    margin: 0 auto;
    max-width: 62.5rem;
}

/* Intro Sesction */
#intro {
    padding-bottom: 10rem;
    color: #fff;
    min-height: 150vh;
    text-align: left;
    background-color: #000;
}

#intro p {
    font-size: 1.1rem;
    font-family: 'VT323', monospace;

}

#intro h1{
    overflow: hidden;
    white-space: nowrap;
    font-size: 4.5vw;
    font-family: 'VT323', monospace;
    /*border-right: .15em solid white;*/

    -webkit-animation: typing 5s steps(75),
    blink-caret .75s step-end infinite;

            animation: typing 5s steps(75),
    blink-caret .75s step-end infinite;

}

/* The typing effect */
@-webkit-keyframes typing {
  from { width: 0}
  to { width: 100%;}
}
@keyframes typing {
  from { width: 0}
  to { width: 100%;}
}


/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: white; }
}
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: white; }
}

#intro a {
    width: 50%;
    margin: 0 auto;
}

.down-arrow {
    margin: 5rem 0;
    margin:0 auto;
    text-align: center;
    position: static;
}

.down-arrow:hover {
    cursor: pointer;
}

.bounce {
    -webkit-animation: bounce 3s infinite;
    animation: bounce 3s infinite;
}

.no-display {
    display:none;
}
  
@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    40% {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
    }
    60% {
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
    }
}

@-webkit-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    40% {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
    }
    60% {
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
    }
}

@media only screen and (max-width:750px) {
    .down-arrow {
        display: none;
    }
}

 /*Skills Section*/

#skills {
    color: #fff;
    min-height: 175vh;
    background-color: #000;
    margin-top: -10rem;
}

#skills h1{
    font-size: 3rem!important;
    font-family: 'VT323', monospace;
}

#skills h3{
    font-size: 1.5rem!important;
    font-family: 'VT323', monospace;
}

#skills .box section {
    margin: 2rem 0
}

.skills a {
    margin: .5rem!important;
    color: #000;
}

#skills p {
    font-size: 1.2rem;
    font-family: 'VT323', monospace;
}

#skills ul {
    margin: 0;
    padding: 0
}

#skills li{
    font-size: 1.07rem;
}

/* Footer Section */
#footer {
    background-color: #000;
    padding-top: 7.5rem
}

#footer,
#footer a svg {
    color: #fff;
    text-align: center
}

#footer a svg {
    height: 1.5rem;
    width: 2.25rem;
    padding: .25rem 0;
    margin: 5px;
    border: 2px #000; /* TODO */
    border-radius: .25rem;
    transition: all .2s ease-in-out;
}

#footer a svg:hover {
    color: #000;
    background-color: #fff; /* TODO */
    border-color: #fff
}

#footer .home-section{
    background: #fff;
}

#footer_name{
    font-size: 1.4rem;
    font-family: 'VT323', monospace;
}
.box {
    display: flex;
    flex-direction: row;
    border: 1px solid var(--border-color);
    border-radius: 1rem;
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, .2);
    background-color: var(--lighter-background-color)
}

.box svg {
    color: var(--accent-color);
}

@media only screen and (max-width:750px) {
    .box {
        flex-direction: column
    }
}
/* Resume Section */
#resume {
    font-size: 1.2rem;
    color: #fff;
    background-color: #000;
    height: 100%

}

#resume h1 {
    font-size: 3em;
    font-family: 'VT323', monospace;
}

/* Section body changes */
.resume-section .section-body {
    padding: 0;
    margin: 0; 
    width: 100%;
    height: 100%;
    box-sizing: inherit;
    display: flex;
    flex-direction: row;
}

@media only screen and (max-width: 750px) {
    .resume-section .section-body {
        flex-direction: column;
    }
}

.resume-section-body {
    float: right;
    height: 100%;
    display: block;
    font-family: 'VT323', monospace;
}

.resume-section h2 {
    float: left;
    min-width: 25rem;
    padding-bottom: 0;
    margin-bottom: 0;
    font-family: 'VT323', monospace;
}

.resume-section-body p {
    margin: 0;
}

.resume-section-item ul {
    padding-left: 1.25rem;
    padding-top: 0;
    margin-top: 0
}

.resume-section-item ul li {
    word-wrap: break-word
}

.resume-section-item h3 {
    margin-bottom: 0;
    color: #fff;
    /*color: rgb(26, 25, 25);*/
}

.resume-section-item h4 {
    margin-top: 0;
    margin-bottom: 0
}

.resume-section-item p a {
    color: blue;
    text-decoration: none;
}

.resume-button-link {
    text-decoration: none
}

.resume-button-link:hover {
    text-decoration: none;
    font-size: 1.2rem;
}

.resume-button {
    max-width: 100%;
    margin: auto;
    transition: all .2s ease-in-out;
    margin-top: 3rem;
    margin-bottom: -10rem;
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, .2);
    position: absolute;
    display: block;
}

@media only screen and (max-width:750px) {
    .resume-button {
        text-align: center
    }
}

.resume-button-text {
    margin-left: -15rem;
    margin-top: 3rem
}

@media only screen and (max-width:750px) {
    .resume-button-text {
        margin-left: 0;
        margin-top: -2rem
    }
}



.resume-button:hover{
    cursor: pointer;
}

.box article {
    flex-grow: 1;
    flex-basis: 0;
    -ms-flex-positive: 1;
    -ms-flex-preferred-size: 0;
    display: block;
    padding: 2rem;
    border: solid 1px;
    background-color: #000;
    color: #fff;
    font-family: 'VT323', monospace;
}
.box article a{
    
    color: white;
}

@media only screen and (max-width:750px) {
    .box article {
        border-right: 0 solid var(--border-color);
        border-bottom: 1px solid var(--border-color);
    }
}

.info-text input:focus {
    outline: none;
}

.info-body input {
    margin: 0;
    background-color: black;
    color: white;
    border: none;
    width: 90%;
    padding: 0;
    font-size: 1rem;
    font-family: 'VT323', monospace;
    overflow-wrap: break-word;
    word-break: break-all;
}

.info-text p {
    overflow-wrap: break-word;
    word-break: break-all;
}

.info-text p a {
    font-weight: bold;
    color: white;
}
.info {
    width: 63rem;
    margin: 7.5vh auto 0;
    margin-top: 3vh;
    padding-top: 0.5rem;
    background-color: white;
    border-style: solid;
    font-family: 'VT323', monospace;
    text-align: left;
    box-shadow: 4px 4px 4px 0 rgba(0, 0, 0, .2);
}

.info-body {
    background-color: #000;
    color: #fff;
    padding: 0.5rem;
    margin: 0 auto;
    height: 100%;
    overflow: hidden;
}

.info-text {
    height: 22.5rem;
    width: 100%;
    overflow-y: scroll;
    scroll-behavior: smooth;
    padding-right: 3rem; /* Increase/decrease this value for cross-browser compatibility */
    box-sizing: content-box;
    box-sizing: initial; /* So the width will be 100% + 17px */
    color: #fff;
}

.info-text p {
    font-size: 1rem !important;
    margin: 3px auto;
    color: #fff;
}

.info-carret {
    font-size: 1rem;
    
}

@media only screen and (max-width:800px) {
    .info {
        width: 100%;
    }
}
html {
  --border-color: rgba(0,0,0, 0.125); /* Safari */
  transition: opacity 2s;
  transition-timing-function: ease-in-out;
}


html[data-theme='light'] {
  --background-color: #fff;
  --lighter-background-color: #fff;
  --accent-color: rgb(255, 82, 82);
  --text-color: #000; /* Safari */
  transition: opacity 2s;
  transition-timing-function: ease-in-out;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background-color);
  color: var(--text-color);
}

/* universal classes */
.no-border {
  border: none!important
}
